import React from "react";
import {
  List,
  Datagrid,
  TextField,
  DateField,
  EditButton,
  DeleteButton,
} from "react-admin";

const UserList = (props) => {
  return (
    <List {...props}>
      <Datagrid>
        <TextField source="id" />
        <TextField label="" source="name" />
        <TextField source="email" />
        <TextField label=" " source="phoneNumber" />
        <TextField label="" source="balance" />
        <TextField label=" " source="prefferedBankAccount.bankName" />
        <TextField label="" source="prefferedBankAccount.bankAccount" />
        <TextField source="isPriveleged" />

        {/* <DateField source="createdAt" /> */}
        <EditButton basePath="/User" />
        <DeleteButton basePath="/User" />
      </Datagrid>
    </List>
  );
};
export default UserList;
