import React from "react";
import { Edit, SimpleForm, TextInput } from "react-admin";

const UserEdit = (props) => {
  return (
    <Edit title={"Edit User"} {...props}>
      <SimpleForm>
        <TextInput disabled source="id" />
        <TextInput label="" source="name" />
        <TextInput source="email" />
        <TextInput label=" " source="phoneNumber" />
        <TextInput label="" source="balance" />
        <TextInput label=" " source="prefferedBankAccount.bankName" />
        <TextInput label="" source="prefferedBankAccount.bankAccount" />
        <TextInput source="isPriveleged" />
      </SimpleForm>
    </Edit>
  );
};

export default UserEdit;
